<template>
  <div>
    <ik-data-table
      ref="thresholdColor"
      :model="model"
      :entity-name="$t('')"
      :icon="'mdi-heart'"
    >
      <template v-slot:footer>
        <v-btn
          class="red ml-2"
          @click="reset"
        >
          {{ $t('') }}
        </v-btn>
      </template>
    </ik-data-table>
  </div>
</template>

<script>
  import { IkDataTable } from 'metaflow-js'
  import DThresholdColorSetting from '../../model/DThresholdColorSetting'

  export default {

    components: {
      IkDataTable,
    },
    data: function () {
      return ({
        model: DThresholdColorSetting,
      })
    },
    methods: {
      async reset () {
        await this.model.resetColorList()
        this.$refs.thresholdColor.reload()
      },
    },
  }
</script>

